#blog {
  .blog-cnt {
    float: left;
    width: 640px;
    margin: 0px 40px 0 0;
  }
  .blog-sidebar {
    float: right;
    width: 280px;
    font-size: 14px;
    .swrap {
      margin: 0 0 30px;
    }
    .ss-ul {
      position: relative;
      padding: 10px 0 0;
    }
    .ss-ulr {
      position: relative;
      padding: 10px 0 0;
      .ss-li {
        padding: 15px 0px;
        line-height: 1.5;
        border-bottom: 1px solid #e2e2e3;
        height: auto;
      }
    }
    .ss-li {
      height: 40px;
      line-height: 40px;
      border-bottom: 1px solid #e2e2e3;
    }
    .select-block {
      position: relative;
      padding: 20px 0;
      select {
        border-radius: 0;
        -webkit-appearance: none;
        -moz-appearance: none;
        appearance: none;
        display: block;
        height: 40px;
        width: 100%;
        padding: 0 0 0 10px;
        background: #e2e2e3 url(../img/pc/select-arrow.png) right center no-repeat;
      }
    }
  }
  &.index {
    .list .li {
      position: relative;
      padding: 0 0 40px;
      border-bottom: 2px solid #e2e2e3;
      margin: 0 0 40px;
    }
    .desc-little {
      font-size: 14px;
    }
    .photo {
      float: right;
      width: 160px;
      margin: 80px 0 0 20px;
    }
    .desc-little {
      line-height: 32px;
      overflow: hidden;
      a {
        text-decoration: underline;
      }
    }
    .paginate {
      text-align: center;
      .page-numbers {
        display: inline-block;
        width: 30px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        box-sizing: border-box;
        border: 1px solid #ff9900;
        margin: 0 2px;
        font-size: 14px;
        &.current {
          background: #ff9900;
          color: #fff;
        }
      }
    }
  }
  .dt {
