      }
    }
    .images {
      position: relative;
      .prev {
        position: absolute;
        top: 50%;
        left: 10px;
        z-index: 10;
        margin-top: -25px;
      }
      .next {
        position: absolute;
        z-index: 10;
        top: 50%;
        right: 10px;
        margin-top: -25px;
      }
      .slider {
        margin: 30px auto;
        background: #000;
